<template>
  <!--  UI  -->
  <v-card
    class="mx-auto rounded-lg"
    min-width="315px"
    max-width="500px"
    width="50vw"
  >
    <v-card-title>
      <span class="text-h5 font-weight-medium"></span>
      <v-spacer></v-spacer>
    </v-card-title>

    <v-divider></v-divider>

    <v-card-text>
      <v-form v-model="valid">
        <v-row class="d-flex justify-center">
          <v-col cols="10" class="px-0">
            <v-text-field
              class="registerForm"
              label="Email"
              v-model="email"
              prepend-icon="mdi-at"
              hint="     ."
              :rules="[isEmail('')]"
            >
            </v-text-field>

            <v-text-field
              class="registerForm"
              v-model="password"
              label="Password"
              prepend-icon="mdi-lock"
              :append-icon="passwordShow ? 'mdi-eye' : 'mdi-eye-off'"
              :type="passwordShow ? 'text' : 'password'"
              @click:append="passwordShow = !passwordShow"
              :rules="[required(''), isLongEnough(), hasKorean()]"
              counter
            >
            </v-text-field>

            <v-text-field
              class="registerForm"
              v-model="passwordConfirm"
              label="Password Confirm"
              prepend-icon="mdi-lock"
              :append-icon="confirmShow ? 'mdi-eye' : 'mdi-eye-off'"
              :type="confirmShow ? 'text' : 'password'"
              @click:append="confirmShow = !confirmShow"
              :rules="[isIdentical(), hasKorean()]"
              counter
            >
            </v-text-field>

            <v-text-field
              class="registerForm"
              v-model="name"
              label="Name"
              prepend-icon="mdi-account"
              :rules="[required(''), hasEnglish()]"
            >
            </v-text-field>

            <!--  Date Picker -->
            <v-menu
              ref="menu"
              v-model="menu"
              :close-on-content-click="false"
              transition="scale-transition"
              offset-y
              min-width="275px"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  class="registerForm"
                  v-model="birthday"
                  label="Birthday date"
                  prepend-icon="mdi-calendar"
                  v-bind="attrs"
                  v-on="on"
                  :rules="[isValidUser()]"
                  readonly
                ></v-text-field>
              </template>

              <v-date-picker
                ref="picker"
                v-model="birthday"
                color="accent"
                :max="new Date().toISOString().substr(0, 10)"
                min="1935-01-01"
                @change="save"
              ></v-date-picker>
            </v-menu>

            <v-select
              class="registerForm"
              v-model="school"
              :items="schoolList"
              label="School"
              prepend-icon="mdi-school"
              :rules="[required(' ')]"
            ></v-select>

            <v-radio-group row v-model="userAuthority" :mandatory="false">
              <template v-slot:label>
                <div>{{ userAuthoritySelectionText }}</div>
              </template>

              <v-radio
                class="mx-auto"
                label=""
                color="success"
                value="isStudent"
              ></v-radio>

              <v-radio
                class="mx-auto"
                label=""
                color="error"
                value="isTeacher"
              ></v-radio>
            </v-radio-group>

            <v-btn
              color="accent"
              block
              :disabled="!valid"
              :loading="isLoading"
              @click="register"
            >
              
            </v-btn>
          </v-col>

          <v-col cols="10" class="text-center">
            <span class="text-secondary">
               ?
            </span>

            <router-link :to="{ name: 'Login' }">
               
            </router-link>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    // Form  
    valid: false,
    passwordShow: false,
    confirmShow: false,
    menu: false,

    schoolList: ["", "", ""],

    /*
     *    
     */
    email: "",
    password: "",
    passwordConfirm: "",
    name: "",
    birthday: "",
    school: "",
    userAuthority: "isStudent",
    // photoFile: null,

    /*
     *  (Validation)  .
     */
    required(propertyType) {
      return value => !!value || `${propertyType} .`;
    },
    //      .
    isEmail(propertyType) {
      const emailRegEx = /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;
      return value =>
        emailRegEx.test(value) || `${propertyType}   .`;
    },
    //   ,      .
    hasKorean() {
      const koreanRegEx = /[-|-|-]/;
      return value => !koreanRegEx.test(value) || "! / .";
    },
    //   ,        .
    hasEnglish() {
      const englishRegEx = /[a-z|A-Z|0-9|\s|!@#$%^&*()-_=+{}<>,./?`~|\s]/gi;
      return value =>
        !englishRegEx.test(value) ||
        " ,    .";
    },
    //  8  .
    isLongEnough() {
      return value =>
        value.length >= 8 || ` 8  .`;
    },
    //     .
    isIdentical() {
      return value =>
        value === this.password || `  .`;
    },
    //  7 ,  135     .
    isValidUser() {
      let maximum = new Date();
      maximum = new Date(maximum.setFullYear(maximum.getFullYear() - 135));
      let minimum = new Date();
      minimum = new Date(minimum.setFullYear(minimum.getFullYear() - 7));

      return value =>
        (minimum > new Date(value) && maximum < new Date(value)) ||
        `      .`;
    }
  }),
  computed: {
    userAuthoritySelectionText() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return " ";
        case "sm":
          return "  ";
        default:
          return "  .";
      }
    },
    isLoading() {
      return this.$store.getters["auth/isLoading"];
    }
  },
  watch: {
    menu(val) {
      val && setTimeout(() => (this.$refs.picker.activePicker = "YEAR"));
    }
  },
  methods: {
    // Date Picker   .
    save(date) {
      this.$refs.menu.save(date);
    },
    //   Axios  .
    register() {
      //  Flag True .
      this.isLoading = true;

      //  Form Data Object  .
      const registerFormData = {
        email: this.email,
        password: this.password,
        password_confirm: this.passwordConfirm,
        name: this.name,
        birthday: this.birthday,
        school: this.school,
        is_student: this.userAuthority == "isStudent" ? true : false
      };

      // Vuex Actions , register .
      this.$store.dispatch("auth/register", registerFormData);
    }
  }
};
</script>

<style scoped>
.registerForm {
  margin: 5px 0px;
  padding: 2px;
}
</style>
